
















import axios from 'axios';
import moment from 'moment';

export default {
  name: 'SubItem',
  props: ['id', 'name', 'completed'],
  data() {
    return {
      editing: false,
      item: {
        id: this.id,
        name: this.name,
        completed: this.completed
      }
    };
  },
  directives: {
    focus: {
      inserted (el) {
        el.focus()
      }
    }
  }
};

