




























    
import axios from 'axios';
import TodoListSummary from '../components/TodoListSummary.vue';

export default {
    name: 'AllTodoLists',
    data() {
        return {
            todoLists: [],
            form: {
                listTitle: ''
            }
        }
    },
    methods: {
        getTodoLists() : void {
            axios({
                method: 'get',
                url: 'http://localhost:5000/accounts/1/lists',
            })
            .then((response) => {
                this.todoLists = response.data;
            }).catch((e) => {
                console.log(e);
            });
        },
        addTodoList(listTitle : string) : void {
            this.$bvModal.hide('modal-add');

            let data = JSON.stringify({
                listTitle
            });

            axios({
                url: 'http://localhost:5000/accounts/1/lists',
                method: 'POST',
                data,
                headers: {
                    'content-type': 'application/json'
                }
            }).then((response) => {
                if(response.status == 200) {
                    this.todoLists.push(response.data);
                    this.form.listTitle = ''
                }
            });
        },
        deleteList(list) : void {
            this.$bvModal.msgBoxConfirm(`Are you sure you want to delete ${list.listTitle}?`, {
                size: 'sm',
                okVariant: 'danger',
                okTitle: 'Delete',
                cancelTitle: 'Cancel',
                footerClass: 'p-2',
                hideHeaderClose: false,
            })
            .then(choseToDelete => {
                if(choseToDelete) {
                    axios({
                        method: 'DELETE',
                        url: `http://localhost:5000/accounts/1/lists/${list.id}`
                    }).then((response) => {
                        let index = this.todoLists.findIndex(({id}) => id === list.id);
                        if(index !== -1) {
                            this.todoLists.splice(index, 1);
                        }
                    });
                }
            })
        }
    },
    created: function() {
        this.getTodoLists();
    },
    components: {
        TodoListSummary
    }
}

