



























































import axios from 'axios';
import TodoItem from './TodoItem.vue';
import draggable from 'vuedraggable';
import ConfettiGenerator from "confetti-js";

export default {
  name: 'TodoList',
  props: ['id'],
  data() {
    return {
      todoList: {},
      todoListLayout: [],
      todoListItems: [],
      form: {},
      listIsEmpty: false,
      editingTitle: false,
      confetti: false
    }
  },
  created: function() {
    this.getTodoList(this.id);
  },
  methods: {
    getTodoList(id : number) : void {
      // Get list
      axios({
        method: 'get',
        url: 'http://localhost:5000/accounts/1/lists/' + id
      }).then((response) => {
        this.todoList = response.data;
      }).catch((e) => {
        console.log(e);
      });

      // Get list layout
      axios({
        method: 'get',
        url: `http://localhost:5000/accounts/1/lists/${id}/layout`
      }).then((response) => {
        this.todoListLayout = response.data;

        // Get todo list items
        axios({
          method: 'get',
          url: 'http://localhost:5000/accounts/1/lists/' + id + '/todos'
        }).then((response) => {

          this.todoListLayout.forEach(position => {
            let index = response.data.findIndex(item => item.id === position);

            if(index !== -1) {
              this.todoListItems.push(response.data[index]);
            }
          });

          if(this.todoListItems.length < 1) {
            this.listIsEmpty = true;
          }
          else {
            this.listIsEmpty = false;
          }
        });
      });
    },
    updateListTitle(listTitle : string) {
      let data = JSON.stringify({ listTitle });

      axios({
        url: `http://localhost:5000/accounts/1/lists/${this.id}`,
        method: 'PUT',
        data,
        headers: {
          'content-type': 'application/json'
        }
      });
    },
    addTodoListItem(name : string, notes : string, dueDate : Date) : void {
      this.$bvModal.hide('modal-add');

      let data = JSON.stringify({
        name,
        notes,
        dueDate
      });

      axios({
        url: `http://localhost:5000/accounts/1/lists/${this.id}/todos`,
        method: 'POST',
        data,
        headers: {
          'content-type': 'application/json'
        }
      }).then((response) => {
          this.todoListItems.unshift(response.data);
          this.form.name = '';
          this.form.notes = '';
          this.listIsEmpty = false;
      });
    },
    deleteTodoListItem(item) : void {
      this.$bvModal.msgBoxConfirm(`Are you sure you want to delete ${item.name}?`, {
          size: 'sm',
          okVariant: 'danger',
          okTitle: 'Delete',
          cancelTitle: 'Cancel',
          footerClass: 'p-2',
          hideHeaderClose: false,
      })
      .then(choseToDelete => {
          if(choseToDelete) {
              axios({
                url: `http://localhost:5000/accounts/1/todos/${item.id}`,
                method: 'DELETE'
              }).then((response) => {
                  let index = this.todoListItems.findIndex(({id}) => id === item.id);
                  if(index !== -1) {
                      this.todoListItems.splice(index, 1);
                  }
              });
          }
      });
    },
    checkIfListCompleted() {
      axios({
        method: 'get',
        url: 'http://localhost:5000/accounts/1/lists/' + this.id
      }).then((response) => {
        if(response.data.completed === true) {
          this.confetti = true;
          var confettiSettings = { target: 'confetti' };
          var confetti = new ConfettiGenerator(confettiSettings);
          confetti.render();
        }
        else {
          this.confetti = false;
        }
      })
    },
    updateItemPosition(e) {
      let position = e.newIndex;
      let itemId = parseInt(e.item.dataset.id);
      let data = JSON.stringify({
        position,
        itemId
      });
      
      axios({
        method: 'PUT',
        url: `http://localhost:5000/accounts/1/lists/${this.id}/layout`,
        data,
        headers: {
          'content-type': 'application/json'
        }
      });
    }
  },
  components: {
    TodoItem,
    draggable
  },
  directives: {
    focus: {
      inserted (el) {
        el.focus()
      }
    }
  }
};

